<template>
  <div class="login-wrap">
    <!-- <i class="iconfont iconguanyuwomen"></i> -->
    <div class="logo"></div>
    <div class="loginForm">
      <div class="formItem">
        <van-field
          v-model="accountNo"
          placeholder="账号"
          :error-message="accountNoErr"
          required
          @keyup.enter.native="loginSubmit"
        >
          <van-icon slot="left-icon" class-prefix="iconfont iconwode-weixuanzhong" />
        </van-field>
      </div>

      <div class="formItem">
        <van-field
          v-model="password"
          type="password"
          placeholder="密码"
          :error-message="passwordErr"
          required
          @keyup.enter.native="loginSubmit"
        >
          <van-icon slot="left-icon" class-prefix="iconfont iconmima" />
        </van-field>
      </div>
      <div class="formItem">
        <van-field
          v-model="serverIP"
          placeholder="服务IP"
          :error-message="serverIPErr"
          required
          @keyup.enter.native="loginSubmit"
        >
          <van-icon slot="left-icon" class-prefix="iconfont iconIP1" />
        </van-field>
      </div>
    </div>
    <div class="footer">
      <van-button
        type="primary"
        :loading="loading"
        @click="loginSubmit"
        :disabled="disabled"
        loading-text="登录中..."
      >登录</van-button>
    </div>
  </div>
</template>

<script>
import { Field, Icon, Button } from "vant";
import md5 from "js-md5";
import { FuncType, sendToAPP } from "@/utils/interFlow";
import { encrypt, decryption } from "@/utils/index";
export default {
  name: "logic",
  components: { [Field.name]: Field, [Button.name]: Button, [Icon.name]: Icon },
  created() {},
  data() {
    return {
      accountNo: this.$store.getters.userName || "",
      accountNoErr: "",
      password: decryption(this.$store.getters.userPassword || ""),
      passwordErr: "",
      serverIP: this.$store.getters.serverIP || "",
      serverIPErr: "",
      disabled: false,
      loading: false,
      msg: "Welcome to Your Vue.js App"
    };
  },
  methods: {
    loginSubmit() {
      this.accountNoErr = "";
      this.passwordErr = "";
      this.serverIPErr = "";
      if (this.accountNo === "") {
        this.accountNoErr = "请输入登录账号";
        return;
      }
      if (this.password === "") {
        this.passwordErr = "请输入登录密码";
        return;
      }
      if (this.serverIP === "") {
        this.serverIPErr = "请输入服务IP";
        return;
      }
      this.$store.commit("SET_SERVERIP", this.serverIP);
      if (this.loading) return;
      this.loading = true;
      this.disabled = true;
      this.$store
        .dispatch("LoginByUser", {
          name: this.accountNo,
          pwd: md5(this.password.toUpperCase()),
          from: "22"
        })
        .then(res => {
          if (res.Result) {
            this.$store.commit("SET_USERPASSWORD", encrypt(this.password));
            this.$router.push("/index");
            sendToAPP(FuncType.hadLogin, "");
          } else if (res.Info) {
            this.$toast("账号或密码错误");
          } else {
            this.$toast("账号或密码错误");
          }
          this.disabled = false;
          this.loading = false;
        })
        .catch(err => {
          this.disabled = false;
          this.loading = false;
        });
    }
  }
};
</script>

<style lang="less">
.login-wrap {
  width: 100%;
  height: 100%;
  background: url("../assets/bg_login.png");
  background-size: cover;
  background-repeat: no-repeat;
  overflow: hidden;
  .logo {
    width: 28vw;
    height: 10rem;
    background: url("../assets/logo.png") center;
    background-size: contain;
    background-repeat: no-repeat;
    margin: 6rem 36vw;
  }
  .loginForm {
    width: 100%;
    margin: 0 15vw;
    text-align: left;
    .formItem {
      width: 100%;
      margin-bottom: 2rem;
      .iconfont {
        font-size: 1.7rem;
        margin-right: 1vw;
        color: #999;
      }
      input {
        width: 64vw;
        font-size: 1.7rem;
        color: #999;
        padding-left: 0.5rem;
        border: none;
        border-bottom: 1px solid rgba(153, 153, 153, 1);
        box-sizing: border-box;
      }
    }
  }
  .footer {
    margin-top: 4rem;
    button {
      min-width: 66vw;
      background: rgba(0, 203, 171, 1);
      border-radius: 5px;
      border: 1px solid #00cbab;
    }
  }
}
</style>



